import Button from "@mui/joy/Button";
import Stack from "@mui/joy/Stack";
import Typography from "@mui/joy/Typography";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

const NavBar = ({ isLoggedIn, onLogout }) => {
	const navigate = useNavigate();
	const handleLogout = () => {
		axios
			.get("198.199.82.251:8000/logout", {
				withCredentials: true,
			})
			.then((response) => {
				if (response.status === 200) {
					onLogout();
					navigate("/login");
				}
			})
			.catch((error) => {
				if (error.name !== "CanceledError") {
					console.log(error);
				}
			});
	};

	return (
		<div>
			<Stack
				direction="row"
				alignItems="center"
				justifyContent="space-between"
				sx={{
					position: "fixed",
					backgroundColor: "#99ccff",
					width: "100vw",
				}}
			>
				<Stack
					direction="row"
					alignItems="end"
					justifyContent="flex-start"
					sx={{ margin: "10px 20px" }}
				>
					<Link to="/" style={{ textDecoration: "none" }}>
						<Typography
							sx={{ padding: "5px" }}
							id="siteLogo"
							borderRadius={10}
							level="h1"
						>
							liveline
						</Typography>
					</Link>
				</Stack>
				<Stack
					direction="row"
					alignItems="end"
					justifyContent="flex-end"
					sx={{ margin: "10px 20px" }}
				>
					{isLoggedIn ? (
						<Button variant="plain" size="lg" onClick={handleLogout}>
							Log out
						</Button>
					) : (
						<>
							<Link to="/login">
								<Button variant="plain" size="lg">
									Login
								</Button>
							</Link>
							<Link to="/register">
								<Button variant="plain" size="lg">
									Register
								</Button>
							</Link>
						</>
					)}
				</Stack>
			</Stack>
		</div>
	);
};

export default NavBar;
