import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import Button from "@mui/joy/Button";
import Input from "@mui/joy/Input";
import FormControl from "@mui/joy/FormControl";
import FormLabel from "@mui/joy/FormLabel";
import Card from "@mui/joy/Card";
import FormHelperText from "@mui/joy/FormHelperText";

const CredentialInput = ({ submitTarget, onSuccess }) => {
	const [user, setUser] = useState({ username: "", password: "" });
	const [inputInvalid, setInputInvalid] = useState({
		username: false,
		password: false,
	});
	const navigate = useNavigate();

	const handleChange = (event) => {
		const { name, value } = event.target;
		setInputInvalid((prev) => ({ ...prev, [name]: false, incorrect: false }));
		setUser((prevUser) => ({ ...prevUser, [name]: value }));
	};

	const validateUser = (user) => {
		const { username, password } = user;
		let valid = true;
		if (username === "") {
			setInputInvalid((prev) => ({ ...prev, username: true }));
			valid = false;
		}
		if (password === "") {
			setInputInvalid((prev) => ({ ...prev, password: true }));
			valid = false;
		}
		return valid;
	};

	const handleSubmit = (event) => {
		event.preventDefault();
		const { username, password } = user;

		if (!validateUser(user)) {
			return;
		}
		setUser({ username: "", password: "" });
		axios
			.post(submitTarget, { username, password }, { withCredentials: true })
			.then((response) => {
				onSuccess();
				navigate("/secret/okay");
			})
			.catch((error) => {
				setInputInvalid((prev) => ({ ...prev, incorrect: true }));
				console.log(error);
			});
	};

	return (
		<form onSubmit={handleSubmit}>
			<Card sx={{ width: 400 }}>
				<FormControl error={inputInvalid.username || inputInvalid.incorrect}>
					<FormLabel>username</FormLabel>
					<Input
						type="text"
						name="username"
						onChange={handleChange}
						value={user.username}
						placeholder="username"
					/>
					{inputInvalid.username && (
						<FormHelperText>Username cannot be blank</FormHelperText>
					)}
				</FormControl>
				<FormControl error={inputInvalid.password || inputInvalid.incorrect}>
					<FormLabel>password</FormLabel>
					<Input
						type="password"
						name="password"
						onChange={handleChange}
						value={user.password}
						placeholder="password"
					/>
					{inputInvalid.password && (
						<FormHelperText>Password cannot be blank</FormHelperText>
					)}
					{inputInvalid.incorrect && (
						<FormHelperText>Incorrect username or password</FormHelperText>
					)}
				</FormControl>
				<Button type="submit">Submit</Button>
			</Card>
		</form>
	);
};

export default CredentialInput;
