import { useState } from "react";
import { Outlet, useNavigate } from "react-router-dom";
import Auth from "../components/Auth";

const Secret = () => {
	const [isAuth, setIsAuth] = useState(false);
	const navigate = useNavigate();

	return (
		<div>
			<Auth
				onSuccess={() => {
					setIsAuth(true);
				}}
				onError={() => {
					navigate("/login");
				}}
			/>
			{isAuth && <Outlet />}
		</div>
	);
};

export default Secret;
